e ® symbol on your trademark. You will also have the ability to show others of your ownership to the brand, making it less likely for others to consume. Under common law, being the first to use a specific trademark also comes with certain rights that may stop others from using your mark. However, this can be risky because it does not come with as many benefits as a registered trademark would.")]),_vm._v(" "),_c('p',[_c('b',[_vm._v("TIP")])]),_vm._v(" "),_c('p',[_vm._v("The more unique a trademark is, the less chance it has for it to be duplicated, which means more protection. It is also good to know that successful marks are normally those that have no relation to the service or goods.\n                                    ")]),_c('b',[_vm._v("Trademark Registration")]),_vm._v(" "),_c('p',[_vm._v("Before adopting a new mark, it is advisable to have a search conducted and evaluated by a qualified attorney. Someone else may already have rights to virtually the same mark, and it is better to know about that or other potentially conflicting marks before you have made an investment in your new mark. Lack of knowledge is normally not a defense to trademark infringement. The extent of searching that is warranted depends on the nature of the mark, the goods or services for which it is to be used, and the countries in which the mark is intended to be used. Basic searches typically focus on trademark registration databases. Usually, an application to register a trademark is made to the national trademark office in the country or region where the applicant wishes to protect the mark. The application will usually include the name and address of the applicant, a representation of the mark, a listing of the goods or services that are desired to be covered by the registration, and the official filing fee. In some jurisdictions, it may also be necessary to file specimens showing how the mark is used. However, most countries do not require a mark to be used before it is registered.")